.wp-block-create-block-oet-footnotes {
	background-color: #21759b;
	color: #fff;
	padding: 2px;
}
.wp-block-oet-block-oet-footnotes .wp-block-separator { 
    border-bottom: 1px solid #cccccc !important;
    margin-bottom: 0;
}
#footnote_box {
  font-size: 14px;
  background: #222;
  color: #fff;
  padding: 5px 15px;
  margin: 10px;
  border: 1px solid #888;
  box-shadow: 0px 0px 5px #888;
}
#footnote_box a { color:#FFFFFF; }